.autor {
  background-color: white;
  color: #3e3e3e;
  width: 100%;
  position: relative;
  bottom: 70px;
  border-radius: 1.5rem;
  display: flex;
  flex-direction: row-reverse;
  padding: 1rem;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 2rem;
  padding-bottom: 2rem;
  transform: scale(80%);
  @media all and (min-width: 768px) {
    transform: scale(95%);
    margin: auto;
    max-width: 700px;
    width: 90%;
  }
  &__info-project {
    width: 60%;
    &--subtitle {
      font-size: 20px;
      font-weight: bold;
      text-align: center;
      margin-bottom: 0.9rem;
    }

    &--title {
      font-size: 18px;
      color: black;
      margin-bottom: 10px;
    }
    &--slogan {
      font-size: 16px;
      margin-bottom: 10px;
    }
    &--desc {
      font-size: 16px;
      margin-bottom: 15px;
    }
    &--technologies {
      background-color: lightgray;
      border-radius: 20px;
      font-size: 16px;
      padding: 5px;
      text-align: center;
      margin-bottom: 15px;
    }
    &--icons {
      font-size: 20px;
      text-align: center;
      padding-top: 7px;
      cursor: pointer;
      color: black;
      .fa-github-alt {
        padding-right: 10px;
      }
    }
  }
  &__info-autor {
    width: 35%;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &--image {
      border: solid 2px #302c42;
      border-radius: 20%;
      width: 100px;
      height: 160px;
      margin-bottom: 5px;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      @media (min-width: 768px) {
        width: 140px;
        height: 200px;
      }
    }
    &--job {
      font-weight: bold;
      color: black;
      font-size: 1rem;
      margin-bottom: 5px;
    }
    &--name {
      font-size: 1rem;
    }
  }
}

.cardLink {
  color: black;
}

.hidden {
  display: none;
}
